import {barrierTexture} from './textures.js'
import { isCollision } from './collision.js'
import {Sprite} from 'pixi.js'
function addBarrier(index,game,x,y){
    const data=barrierTexture[index]
    const barrier=new Sprite(data.texture)
    barrier.width=data.width
    barrier.height=data.height
    barrier.x=x || data.x || game.app.screen.width
    barrier.y=y || game.groundHeight-barrier.height+10-data.y || game.groundHeight-barrier.height+10
    barrier._run=data._run?data._run:(sprite,time)=>{
        sprite.x-=game.speed*time
        if(isCollision(sprite,game.dino)){
            game.end()
        }
    }
    game.barrierContainer.addChild(barrier)
    return barrier
}
function startAddBarrier(game){
    game.addBarrierTimer=setTimeout(()=>{
        addBarrier(
            Math.floor(Math.random()*barrierTexture.length),
            game,
        ).collision=true
        startAddBarrier(...arguments)
    },(Math.random()*5+5)*200)
}
function removeBarriers(game){
    game.barrierContainer.children=[]
}
function stopAddBarrier(game){
    clearTimeout(game.addBarrierTimer)
}
function Run(game,time){
    game.barrierContainer.children.forEach(barrier=>{
        barrier._run(barrier,time)
    })
}
let _Run
function startBarrierRun(game){
    _Run=time=>Run(game,time)
    game.app.ticker.add(_Run)
}
function stopBarrierRun(app){
    app.ticker.remove(_Run)
}
export {
    addBarrier,
    startAddBarrier,
    removeBarriers,
    stopAddBarrier,
    startBarrierRun,
    stopBarrierRun
}