import * as PIXI from 'pixi.js'
//引入纹理
import {standing,running_animation,jumping,groundTexture,dead,game_over,game_reload} from './textures.js'
//创建精灵
const dino=new PIXI.Container()
dino.x=100
dino.collision=true
const dino_standing=new PIXI.Sprite(standing)
const dino_running=new PIXI.AnimatedSprite(running_animation)
dino_running.animationSpeed=0.1
dino_running.play()
const dino_jumping=new PIXI.Sprite(jumping)
const dino_dead=new PIXI.Sprite(dead)
const ground=new PIXI.TilingSprite(groundTexture,window.innerWidth,15)
const barrierContainer=new PIXI.Sprite()
const gameover=new PIXI.Sprite(game_over)
const gameReload=new PIXI.Sprite(game_reload)
gameover.visible=false
gameReload.visible=false
const gameStartText=new PIXI.Text('开始游戏 —— Enter\n跳跃 —— Up\n开发者 —— YUAN Teacher',{
    align:'center',
    fontSize:16,
    fill:'gray'
})
//切换皮肤函数
function toggleSkin(container,sprite){
    container.children.forEach(ele=>ele.alpha=0)
    sprite.alpha=1
    container.skin=sprite
}
//设置高度函数
function setGroundHeight(height,game){
    ground.y=height
    dino.y=height-dino.height+10
    game.groundHeight=height
}
//将皮肤添加到容器中
dino.addChild(dino_standing,dino_running,dino_jumping,dino_dead)
//导出
export {
    dino,
    dino_standing,
    dino_running,
    dino_jumping,
    dino_dead,
    ground,
    barrierContainer,
    gameover,
    gameReload,
    gameStartText,
    toggleSkin,
    setGroundHeight
}