export function isCollision(a,b){
    let bw=b.collisionWidth || b.width
    if(b.x+bw<a.x)return false
    let aw=a.collisionWidth || a.width
    let ah=a.collisionHeight || a.height
    
    let bh=b.collisionHeight || b.height
    return (
        a.collision &&
        b.collision &&
        a.x<=b.x+bw &&
        a.x+aw>=b.x &&
        a.y+ah>=b.y &&
        a.y<=b.y+bh 
    )
}
