<template>
  <div id="app">
    
  </div>
</template>

<script setup>
  //引入PIXIJS
  import * as PIXI from 'pixi.js'
  //引入恐龙的精灵和地面
  import {
    dino,
    dino_standing,
    dino_running,
    dino_jumping,
    dino_dead,
    ground,
    gameStartText,
    toggleSkin,
    setGroundHeight,
    barrierContainer,
    gameover,
    gameReload,
    
  } from './init.js'
  
  import {startAddBarrier,startBarrierRun,stopAddBarrier,stopBarrierRun} from './barrier.js'
  import { keyMap,jump,stopJump } from './keys.js'
  //初始化APP
  const app=new PIXI.Application({
    width:innerWidth,
    height:innerHeight,
    backgroundColor:0xffffff,
    antialias:true,
    resolution:devicePixelRatio || 1
  })
  document.body.appendChild(app.view)
  gameStartText.x=app.screen.width/2-gameStartText.width/2
  gameover.x=app.screen.width/2-gameover.width/2
  gameover.y=100
  gameReload.x=app.screen.width/2-gameReload.width/2
  gameReload.y=150
  dino.collisionWidth=40
  dino.collisionHeight=40
  const game={
    speed:8,
    jumpHeight:150,
    dino,
    app,
    isStopping:true,
    _run(time){
      ground.tilePosition.x-=game.speed*time
    },
    start(){
      barrierContainer.children=[]
      this.cont()
    },
    stop(){
      stopAddBarrier(game)
      stopBarrierRun(app)
      stopJump(game)
      app.ticker.remove(this._run)
      dino_running.stop()
      this.isStopping=true
    },
    end(){
      toggleSkin(dino,dino_dead)
      this.stop()
      gameover.visible=true
      gameReload.visible=true
    },
    cont(){
      this.isStopping=false
      toggleSkin(dino,dino_running)
      app.ticker.add(this._run)
      startAddBarrier(game)
      startBarrierRun(game)
      dino_running.play()
      gameover.visible=false
      gameReload.visible=false
      console.log(dino.isJumping);
      if(dino.isJumping){
        jump(game,null,game.groundHeight-dino.height+10)
      }
    },
    barrierContainer
  }
  //设置地面高度为300
  setGroundHeight(300,game)
  //切换恐龙形态
  toggleSkin(dino,dino_standing)
  ground.width=app.screen.width
  //将恐龙和地面添加到APP中
  app.stage.addChild(dino,ground,barrierContainer,gameStartText,gameover,gameReload)
  //监听游戏开始事件
  window.addEventListener('keyup',function start(e){
    if(e.key==='Enter'){
      //调用开始函数
      game.isStopping && game.start()
      window.removeEventListener('keyup',start)
    }
  })
  window.addEventListener('keydown',e=>{
    if(keyMap[e.key]==='jump' && !game.dino.isJumping && !game.isStopping){
      let skin=dino.skin
      jump(game,()=>{
        toggleSkin(dino,skin)
      })
      toggleSkin(dino,dino_jumping)
    }
  })
  window.addEventListener('touchstart',()=>{
    if(!game.dino.isJumping && !game.isStopping){
      let skin=dino.skin
      jump(game,()=>{
        toggleSkin(dino,skin)
      })
      toggleSkin(dino,dino_jumping)
    }
  })
  app.view.addEventListener('touchstart',function start(){
    game.isStopping && game.start()
    app.view.removeEventListener('click',start)
  })
  gameReload.interactive=true
  gameReload.on('click',()=>{
    game.start()
  })
  gameReload.on('touchstart',()=>{
    game.start()
  })
</script>

<style>
*{
  margin: 0;
  padding: 0;
}
canvas{
  width: 100%;
  height:95%;
}
</style>
