const keyMap={
    ArrowUp:'jump',
    ArrowDown:'down'
}
let deg=0
let _jump=null
function jump(game,onend,startY){
    game.dino.isJumping=true
    let speed=180/game.jumpHeight/2*game.speed
    let sy=startY || game.dino.y
    // let dur=game.jumpDuration
    _jump=function (delta){
        deg+=speed*delta
        game.dino.y=sy-Math.sin(deg*Math.PI/180)*game.jumpHeight/2
        if(deg>=180){
            deg=0
            game.dino.y=sy
            game.app.ticker.remove(_jump)
            game.dino.isJumping=false
            onend && onend()
        }
    }
    game.app.ticker.add(_jump)
}
function stopJump(game){
    game.app.ticker.remove(_jump)
}
export {
    keyMap,
    jump,
    stopJump,
}